.content {
    width: 100%;
    height: 100vh;
    color: white;
    font-family: 'Poppins', sans-serif;
    display: flex;
    &__side{
        height: 100%;
        background-color: white;
        .menu-container {
            ul {
                list-style: none;
                position: relative;
                li {
                    border-radius: 5px;
                    display: block;
                    background-color: white;
                    color: black;
                    padding: 2px 12px;
                    font-family: sans-serif;
                    font-size: 1rem;
                    border-bottom: solid black 1px;
                    cursor: pointer;
                    &:hover {
                        transform: scale(1.05);
                    }
                }

            }
        }
    }
    &__tasks{
        flex: 1;
        height: 100%;
        background-color: lightgray;
    }
}